<template>
<div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
        <el-row :gutter="20">
            <el-col :span="8">
                <!-- 搜索框 -->
                <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @clear="clearInp">
                    <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
                </el-input>
            </el-col>
            <el-col :span="4">
                <el-button type="primary" @click="addGoods">添加商品</el-button>
            </el-col>
        </el-row>
        <!-- 表格区域 -->
        <el-table :data="listData" stripe border style="width: 100%">
            <el-table-column label="#" type="index"></el-table-column>
            <el-table-column prop="goods_name" label="商品名称"></el-table-column>
            <el-table-column prop="goods_price" label="商品价格(元)" width="95px"></el-table-column>
            <el-table-column prop="goods_weight" label="商品重量" width="70px"></el-table-column>
            <el-table-column prop="add_time" label="创建时间" width="160px">
                <template slot-scope="scope">
                    {{scope.row.add_time | dataFormat}}
                </template>
            </el-table-column>
            <el-table-column label="操作" width="130px">
                <template slot-scope="scope">
                    <el-button type="primary" size="mini" icon="el-icon-edit"></el-button>
                    <el-button type="danger" size="mini" icon="el-icon-delete" @click="deleteGoods(scope.row.goods_id)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页功能 -->
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[5, 10, 15, 20]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="queryInfo.total" background>
        </el-pagination>
    </el-card>
</div>
</template>

<script>
export default {
  data() {
    return {
    //  表格数据
      listData: [],
      //   查询参数
      queryInfo: {
        //   查询参数
        query: '',
        // 页码
        pagenum: 1,
        // 每页条数
        pagesize: 5,
        // 数据总数
        total: 0
      }
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    async getGoodsList() {
      const { data: res } = await this.$http.get('goods', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('数据列表获取失败！')
      }
      this.listData = res.data.goods
      this.queryInfo.total = res.data.total
    },
    // 每页显示数据
    handleSizeChange(newpage) {
      this.queryInfo.pagesize = newpage
      this.getGoodsList()
    },
    // 当前页
    handleCurrentChange(currentpage) {
      this.queryInfo.pagenum = currentpage
      this.getGoodsList()
    },
    // 清空搜索框触发事件
    clearInp() {
      this.getGoodsList()
    },
    // 删除商品
    async deleteGoods(gId) {
      const confirmResult = await this.$confirm('此操作将永久删除该商品?, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已经取消删除!')
      }
      const { data: res } = await this.$http.delete('goods/' + gId)
      if (res.data.status !== 200) {
        return this.$message.error('商品删除失败！')
      }
      this.$message.success('商品删除成功！')
      this.getGoodsList()
    },
    // 添加商品
    addGoods() {
      this.$router.push('goods/add')
    }
  }
}
</script>

<style lang="less" scoped>
.el-breadcrumb {
    margin-bottom: 15px;
}
.el-table {
    margin-top: 15px;
}
</style>
