<template>
<div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片 -->
    <el-card>
        <el-alert title="添加商品信息" type="info" center show-icon :closable="false"></el-alert>
        <!-- 步骤条 -->
        <el-steps :active="activeIndex - 0" finish-status="success" align-center>
            <el-step title="基本信息"></el-step>
            <el-step title="商品参数"></el-step>
            <el-step title="商品属性"></el-step>
            <el-step title="商品图片"></el-step>
            <el-step title="商品内容"></el-step>
            <el-step title="完成"></el-step>
        </el-steps>
        <!-- 侧边tab栏 -->
        <!-- 表单 -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="150px">
            <el-tabs v-model="activeIndex" tabPosition="left" :before-leave="beforeTabLeave" @tab-click="clickTab">
                <el-tab-pane label="基本信息" name="0">
                    <!-- 表单行 -->
                    <el-form-item label="商品名称" prop="goods_name">
                        <el-input v-model="addForm.goods_name"></el-input>
                    </el-form-item>
                    <el-form-item label="商品价格" prop="goods_price">
                        <el-input v-model="addForm.goods_price" type="number"></el-input>
                    </el-form-item>
                    <el-form-item label="商品重量" prop="goods_weight">
                        <el-input v-model="addForm.goods_weight" type="number"></el-input>
                    </el-form-item>
                    <el-form-item label="商品数量" prop="goods_number">
                        <el-input v-model="addForm.goods_number" type="number"></el-input>
                    </el-form-item>
                    <el-form-item label="商品分类" prop="goods_cat">
                        <!-- 级联 -->
                        <el-cascader v-model="addForm.goods_cat" :options="cateList" :props="props" @change="handleChange"></el-cascader>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="商品参数" name="1">
                    <el-form-item :label="item.attr_name" v-for="(item,index) in cateManyData" :key="index">
                        <!-- 多选框组件 -->
                        <el-checkbox-group v-model="item.attr_vals">
                            <el-checkbox border :label="ele" v-for="(ele,i) in item.attr_vals" :key="i">
                            </el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="商品属性" name="2">
                    <el-form-item :label="item.attr_name" v-for="item in cateOnilData" :key="item.attr_id">
                        <el-input :value="item.attr_vals"></el-input>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="商品图片" name="3">
                    <!-- 图片上传组件 -->
                    <el-upload :action="imgUpladUrl" :headers="headerObj" :on-preview="handlePreview" :on-remove="handleRemove" list-type="picture" :on-success="handleSuccess">
                        <el-button size="small" type="primary">点击上传</el-button>
                    </el-upload>
                </el-tab-pane>
                <el-tab-pane label="商品内容" name="4">
                    <quill-editor ref="myQuillEditor" v-model="addForm.goods_introduce"></quill-editor>
                    <el-button type="primary" class="top" @click="add">添加商品</el-button>
                </el-tab-pane>
            </el-tabs>
        </el-form>
        <!-- 图片点击图片预览 -->
        <el-dialog title="图片预览" :visible.sync="imgDialogVisible" width="50%">
            <img :src="imgSrc" alt="" class="img-size">
        </el-dialog>
    </el-card>
</div>
</template>

<script>
import _ from 'lodash'

export default {
  data() {
    return {
      activeIndex: '0',
      //   表单数据
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        // 级联选择器数据
        goods_cat: [],
        // 图片临时存储路径
        pics: [],
        // 商品内容
        goods_introduce: '',
        // 商品的动态和静态数据数组
        attrs: []
      },
      // 表单校验规则
      addFormRules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '请选择商品分类', trigger: 'blur' }
        ]
      },
      //   分类列表数据
      cateList: [],
      //   级联选择器参数
      props: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 动态参数列表
      cateManyData: [],
      // 静态属性列表
      cateOnilData: [],
      // 图片上传地址
      imgUpladUrl: 'http://127.0.0.1:8888/api/private/v1/upload',
      // token图片上传组件头
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 图片对话框
      imgDialogVisible: false,
      // 预览图片路径
      imgSrc: ''
    }
  },
  methods: {
    //   获取分类列表
    async getCates() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('分类列表获取失败！')
      }
      this.cateList = res.data
    },
    //   级联选择器改变事件
    handleChange() {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
        return
      }
      this.getCates()
    },
    // 切换标签之前的钩子，若返回 false 则阻止切换。
    async beforeTabLeave(activeName, oldactiveName) {
      if (oldactiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('请先选择商品分类！')
        return false
      } else if (oldactiveName === '1') {
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, { params: { sel: 'only' } })
        if (res.meta.status !== 200) {
          return this.$message.error('静态属性请求失败！')
        }
        this.cateOnilData = res.data
      }
    },
    // 点击侧边标签切换
    async clickTab() {
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, { params: { sel: 'many' } })
      if (res.meta.status !== 200) {
        return this.$message.error('动态参数请求失败！')
      }
      res.data.forEach(ele => {
        ele.attr_vals = ele.attr_vals.length === 0 ? [] : ele.attr_vals.split(' ')
      })
      this.cateManyData = res.data
    },
    // 图片点击显示时触发
    handlePreview(file) {
      this.imgSrc = file.response.data.url
      this.imgDialogVisible = true
    },
    // 图片点击删除时触发
    handleRemove(file) {
      const i = this.addForm.pics.findIndex(item => {
        if (item.pic === file.response.data.tmp_path) {
          return item
        }
      })
      this.addForm.pics.splice(i, 1)
    },
    // 图片上传成功时触发的函数
    handleSuccess(response) {
      if (response.meta.status !== 200) {
        return this.$message.error('图片上传失败！')
      }
      const picInfo = { pic: response.data.tmp_path }
      this.addForm.pics.push(picInfo)
    },
    // 添加商品表单提交按钮
    async add() {
      const form = _.cloneDeep(this.addForm)
      form.goods_cat = form.goods_cat.join(',')
      // 动态参数列表
      this.cateManyData.forEach(item => {
        this.addForm.attrs.push({
          attr_id: item.attr_id,
          attr_value: item.attr_vals
        })
      })
      // 静态属性列表
      this.cateOnilData.forEach(item => {
        this.addForm.attrs.push({
          attr_id: item.attr_id,
          attr_value: item.attr_vals
        })
      })
      form.attrs = this.addForm.attrs
      const { data: res } = await this.$http.post('goods', form)
      if (res.meta.status !== 201) {
        return this.$message.error('商品添加失败！')
      }
      this.$message.success('商品添加成功！')
      this.$router.push('/goods')
    }
  },
  created() {
    this.getCates()
  },
  computed: {
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
.el-form {
    margin-top: 20px;
}
.el-form-item {
    width: 1200px;
}
.el-checkbox {
  margin: 0 10px 0 0!important;
}
.img-size {
  width: 100%;
}
.top {
  margin-top: 15px;
}
</style>
